import { StringKeyValue } from '../common/StringKeyValue';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Integration action describes integration protocol and details to execute 
*/
export class IntegrationAction extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (Empty if integration is associated to the account level) 
    public folderId: string;
 
    // Integration name 
    public name: string;
 
    // The URI encapsulates many of the protocol specification (based on RFC-3986): schema://[user:password]@host:[port]/[path]?[parameters] supported schemas: http | https | smtp | email 
    public uri: string;
 
    // HTTP protocol Operation: GET | POST | PUT (applicable for http/https protocols) 
    public operation: string;
 
    // HTTP protocol Headers (applicable for http/https protocols) 
    public headers: StringKeyValue[];
 
    // SMTP protocol List of recipients (applicable for smtp protocol) 
    public recipients: string[];
 
    // SMTP protocol Message subject template (applicable for smtp protocol) 
    public subject: string;
 
    // Message body template 
    public body: string;
 
    // Message content mime type 
    public mimeType: string;
 
    // Is clip required for the integration 
    public clipRequired: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


